

a.image-reel
{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    display: flex;
    justify-content: space-between;
}


a.image-reel > dialog 
{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

/** previous and next buttons */
a.image-reel > dialog > button
{
    position: absolute;
    top: 0;
    bottom: 0;
    width: 50%;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
}

/** create hidden images

 */


a.image-reel > dialog > li > img.hidden
{
    display: none;
}

/** create visible images

 */

a.image-reel > dialog > li > img.visible
{
    display: block;
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
}

