  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

a.image-reel > dialog > button {
  width: 50%;
  cursor: pointer;
  background-color: #0000;
  border: none;
  outline: none;
  position: absolute;
  top: 0;
  bottom: 0;
}

